.contact-list {
  position: relative;
}

.search {
  position: absolute;
  top: -50px;
  right: 30px;
  width: 20%;
}

h1 {
  font-weight: bold;
  text-align: center;
}

hr {
  width: 5%;
  margin-left: auto;
  margin-right: auto;
  border-color: red;
  border: 2px solid #e60000 !important;
}

/* .Contact-cards {
  display: grid;
  grid-template-columns: auto auto auto;
} */

.plus {
  display: block;
  width: 50px;
  height: 50px;
  margin: 10px;
  display: flex;
  float: right;
  background: linear-gradient(#fff, #fff), linear-gradient(#fff, #fff), #e60000;
  background-position: center;
  background-size: 25% 1px, 1px 25%; /*thickness = 2px, length = 50% (25px)*/
  background-repeat: no-repeat;
  border-radius: 50%;
}
